/* Global Color Scheme */
:root {
  --light-yellow: #fdfd96;
  --gray-brownish: #a8a8a3;
  --light-red: #f28d8d;
  --black-text: #000000;
  --cyan-button: #00bcd4;
  --cyan-line: #00bcd4;
  --gray-line: #808080;
}

/* Global Styles */
body {
  background-color: var(--light-yellow);
  color: var(--black-text);
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  margin: 20px 0;
}

/* Event Input Section */
.event-input {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
}

.event-input input,
.event-input button {
  padding: 10px;
  font-size: 16px;
}

.event-input input[type="text"] {
  width: 250px;
}

.event-input input[type="datetime-local"] {
  width: 250px;
}

.event-input input[type="color"] {
  width: 40px;
}

.event-input button {
  background-color: var(--cyan-button);
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.event-input button:hover {
  background-color: #0097a7;
}

/* Timeline Container */
.timeline-container {
  position: relative;
  width: 100%;
  height: 300px;
  margin-top: 20px;
}

.timeline-line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--cyan-line);
  transform: translateY(-50%);
}

/* Event Styles */
.event {
  position: absolute;
  width: 150px;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  color: white;
  font-size: 14px;
  transition: background-color 0.3s;
}

.event:hover {
  background-color: #e74c3c;
}

.event.future {
  top: 10px;
  background-color: var(--light-red);
}

.event.past {
  bottom: 10px;
  background-color: var(--gray-brownish);
}
